// loader
const body = document.body
window.addEventListener('load', ()=>{
   body.classList.add('loaded')
})
//
if (document.querySelector('img[loading=lazy]')) {
   document.addEventListener('DOMContentLoaded', function () {
      var lazyImages = [].slice.call(document.querySelectorAll('img[loading=lazy]'));
      var active = false;
      const lazyLoad = function () {
         if (active === false) {
            active = true;
            setTimeout(function () {
               lazyImages.forEach(function (lazyImage) {
                  if ((lazyImage.getBoundingClientRect().top <= window.innerHeight && lazyImage.getBoundingClientRect().bottom >= 0) && getComputedStyle(lazyImage).opacity !== '0') {
                     lazyImage.src = lazyImage.dataset.src;
                     lazyImage.srcset = lazyImage.dataset.srcset;
                     lazyImages = lazyImages.filter(function (image) {
                        return image !== lazyImage;
                     });
                     if (lazyImages.length === 0) {
                        document.removeEventListener('scroll', lazyLoad);
                        window.removeEventListener('resize', lazyLoad);
                        window.removeEventListener('orientationchange', lazyLoad);
                     }
                  }
               });
               active = false;
            }, 500);
         }
      };
      document.addEventListener('scroll', lazyLoad);
      window.addEventListener('resize', lazyLoad);
      window.addEventListener('orientationchange', lazyLoad);
   });
}
if (document.querySelector('.burger')) {
   // burger
   const burger = document.querySelector('.burger')
   const menu = document.querySelector('.menu')
   const logo = document.querySelector('.logo')
   const body = document.querySelector('body')
   burger.addEventListener('click', () => {
      if (!menu.classList.contains('active')) {
         menu.classList.add('active')
         logo.classList.add('active')
         burger.classList.add('active')
         body.classList.add('locked')
      } else {
         menu.classList.remove('active')
         logo.classList.remove('active')
         burger.classList.remove('active')
         body.classList.remove('locked')
      }
   })
   function fixedNav() {
      const nav = document.querySelector('nav')
      const breakpoint = 1
      if (window.scrollY > breakpoint) {
         nav.classList.add('fixed__nav')
      } else {
         nav.classList.remove('fixed__nav')
      }
   }
   window.addEventListener('scroll', fixedNav)
}
if (document.querySelector('.body__legend')) {
   // swiper
   const swiperItems = document.querySelectorAll('.legend__item')
   for (var i = 0; i < swiperItems.length; i++) {
      const swiperItem = swiperItems[i].querySelector('.mySwiper')
      const swiperItem2 = swiperItems[i].querySelector('.mySwiper2')
      var swiper = new Swiper(swiperItem, {
         spaceBetween: 30,
         slidesPerView: 6,
         freeMode: true,
         watchSlidesProgress: true,
         loop: !0,
         breakpoints: {
            1138: {
               slidesPerView: 6,
            },
            992: {
               slidesPerView: 5,
            },
            768: {
               slidesPerView: 4,
            },
            650: {
               slidesPerView: 3,
            },
            401: {
               slidesPerView: 2,
            },
            0: {
               slidesPerView: 1,
            },
         },
         navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
         },
      });
      var swiper2 = new Swiper(swiperItem2, {
         spaceBetween: 20,
         cssMode: true,
         thumbs: {
            swiper: swiper,
         },
      });
   }
}
if (document.querySelector('.body__pokedex')) {
   // accordion
   function accordion() {
      const items = document.querySelectorAll('.pokedex__accordion-header')
      items.forEach(item => {
         item.addEventListener('click', () => {
            const parent = item.parentNode
            if (parent.classList.contains('show')) {
               parent.classList.remove('show')
            } else {
               document
                  .querySelectorAll('.pokedex__accordion-item')
                  .forEach(child => child.classList.remove('show'))
               parent.classList.add('show')
            }
         })
      })
   }
   accordion()
   // swiper
   new Swiper('.swiper-container', {
      direction: 'horizontal',
      loop: !1,
      speed: 1500,
      slidesPerView: 1,
      spaceBetween: 34,
      cssMode: true,
      breakpoints: {
         769: {
            cssMode: true,
         },
         0: {
            cssMode: false,
         },
      },
      keyboard: {
         enabled: !0,
         onlyInViewport: !1
      },
      pagination: {
         el: '.swiper-pagination',
         clickable: true,
      },
   });
   // filter menu
   const burger = document.querySelector('.filter')
   const menu = document.querySelector('.pokedex__accordion')
   const logo = document.querySelector('.logo')
   const body = document.querySelector('body')
   burger.addEventListener('click', ()=>{
      if (!menu.classList.contains('active')) {
         menu.classList.add('active')
         logo.classList.add('active')
         burger.classList.add('active')
         body.classList.add('locked')
      } else {
         menu.classList.remove('active')
         logo.classList.remove('active')
         burger.classList.remove('active')
         body.classList.remove('locked')
      }
   })
   // modal
   const btns = document.getElementsByClassName('slide');
   const spans = document.getElementsByClassName('modal__close'); 
   const modals = document.getElementsByClassName('modal__wrapper');
   [...btns].forEach((btn, ind) => {
      btn.onclick = () => (modals[ind].style.display = 'flex');
   });
   [...spans].forEach((span, ind) => {
      span.onclick = () => (modals[ind].style.display = 'none');
   });
   window.onclick = (e) => {
      [...modals].forEach((modal) => {
         if (e.target === modal) {
            modal.style.display = 'none';
         }
      });
   };
}